<template>
  <div id="order-manage">
    <order-nav></order-nav>
    <h3 class="title">工单管理</h3>
    <div class="main">
      <div class="left">
        <p>部门工单统计</p>
        <div class="dpt-data">
          <div class="dpt">
            <span>今日部门工单数量</span>
            <el-input :value="dptInfo.dptToday" readonly></el-input>
          </div>
          <div class="dpt">
            <span>本周部门工单数量</span>
            <el-input :value="dptInfo.dptWeek" readonly></el-input>
          </div>
          <div class="dpt">
            <span>本周部门逾期工单数量</span>
            <el-input :value="dptInfo.dptWeekOver" readonly></el-input>
          </div>
        </div>
        <p>员工工单统计</p>
        <div class="person-data">
          <div>
            <span>时间：</span>
            <el-select v-model="checkedTime" placeholder="请选择">
              <el-option v-for="(item, index) in timeList" :key="index" :label="item" :value="item"></el-option>
            </el-select>
          </div>
          <el-button type="primary" size="small" @click="queryPerson">查询</el-button>
        </div>
        <div class="person-table">
          <el-table
            :data="personData"
            border
            style="width: 50%;margin:0 auto"
            max-height="300"
            size="small"
          >
            <el-table-column prop="0" label="人员">
              <template slot-scope="{row}">
                <el-link @click="showPersonDetail(row[0])">{{row[0]}}</el-link>
              </template>
            </el-table-column>
            <el-table-column prop="1" label="即时"></el-table-column>
            <el-table-column prop="2" label="远期"></el-table-column>
          </el-table>
        </div>
      </div>

      <div class="right">
        <p>逾期工单统计</p>
        <el-table
          :data="overOrderList"
          border
          style="width: 90%;margin:0 auto"
          max-height="400"
          size="small"
        >
          <el-table-column prop="initiate_department" label="发单部门"></el-table-column>
          <el-table-column prop="initiate_person" label="发单人"></el-table-column>
          <el-table-column prop="receive_person" label="接单人"></el-table-column>
          <el-table-column prop="job_type" label="工单类型"></el-table-column>
          <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
          <el-table-column prop="out_time" label="结单时间"></el-table-column>
          <el-table-column prop="need" label="内容"></el-table-column>
          <el-table-column prop="next_info" label="跟进信息"></el-table-column>
        </el-table>
        <el-pagination
          background
          @current-change="overPageChange"
          :current-page.sync="overPage"
          :page-size="50"
          layout="total,prev, pager, next"
          :total="overTotal"
        ></el-pagination>
      </div>
    </div>

    <!-- 员工详情对话框 -->
    <el-dialog
      :title="person+' 员工详情'"
      :visible.sync="personVisible"
      width="80%"
      center
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <el-table :data="personDetail" border style="width: 90%;margin:0 auto" max-height="400">
        <el-table-column prop="initiate_department" label="发单部门"></el-table-column>
        <el-table-column prop="initiate_person" label="发单人"></el-table-column>
        <el-table-column prop="receive_person" label="接单人"></el-table-column>
        <el-table-column prop="job_type" label="工单类型"></el-table-column>
        <el-table-column prop="initiate_time" label="发单时间"></el-table-column>
        <el-table-column prop="out_time" label="结单时间"></el-table-column>
        <el-table-column prop="need" label="内容"></el-table-column>
        <el-table-column prop="next_info" label="跟进信息"></el-table-column>
        <el-table-column prop="status" label="状态"></el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="personPageChange"
        :current-page.sync="personPage"
        :page-size="50"
        layout="total,prev, pager, next"
        :total="personTotal"
      ></el-pagination>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="personVisible = false">关 闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import orderNav from "@/common/order_nav";
export default {
  name: "",
  props: {},
  components: {
    orderNav
  },
  data() {
    return {
      // 部门工单统计
      dptInfo: {
        dptToday: "",
        dptWeek: "",
        dptWeekOver: ""
      },
      // 员工工单统计
      checkedTime: "",
      timeList: ["今天", "本周", "本月"],
      // 员工表格数据
      personData: [],
      // 员工详情表格与分页
      personVisible: false,
      personPage: 1,
      personTotal: 0,
      personDetail: [],
      // 选中的员工
      person: "",

      // 逾期工单统计
      overOrderList: [],
      overPage: 1,
      overTotal: 0
    };
  },
  computed: {},
  watch: {},
  methods: {
    // 获取初始数据
    initData() {
      this.$api.initData(this.overPage).then(res => {
        console.log(res);
        if (res.data.code == 200) {
          // 逾期工单相关
          this.overTotal = res.data.all_num;
          this.overOrderList = res.data.out_time_list;
          // 员工工单相关
          this.personData = res.data.person_list;
          // 部门工单相关
          this.dptInfo.dptToday = res.data.department_list[0];
          this.dptInfo.dptWeek = res.data.department_list[1];
          this.dptInfo.dptWeekOver = res.data.department_list[2];
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    // 逾期工单表格分页
    overPageChange(val) {
      this.overPage = val;
      this.initData();
    },
    // 员工查询
    queryPerson() {
      this.$api.queryPerson(this.checkedTime).then(res => {
        console.log(res);
        if (res.data.code != 200) {
          this.$message.error(res.data.msg);
        }
        this.personData = res.data.person_list;
      });
    },
    // 员工详情数据 封装
    getPersonDetail(name) {
      this.$api.getPersonDetail(name, this.personPage).then(res => {
        console.log(res);
        this.personDetail = res.data.data;
        this.personTotal = res.data.all_num;
        this.personVisible = true;
      });
    },
    // 查看员工详情
    showPersonDetail(name) {
      // 获取员工姓名，方便分页获取
      this.person = name;
      this.getPersonDetail(name);
    },
    // 员工详情分页
    personPageChange(val) {
      this.personPage = val;
      this.getPersonDetail(this.person);
    }
  },
  created() {
    this.initData();
  },
  mounted() {}
};
</script>
<style>
#order-manage .el-date-picker {
  width: 256px;
}
</style>
<style scoped>
#order-manage {
  height: 92vh;
  background: rgba(255, 255, 255, 0.7);
}
.dpt-data,
.person-data {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 12px;
}
.dpt-data .el-input {
  width: 50px;
}
.dpt-data .dpt {
  margin-right: 60px;
}
.person-data div {
  margin-right: 24px;
}
.el-pagination {
  margin-top: 24px;
  text-align: center;
}
.title {
  border-bottom: 1px solid #dcdfe6;
}
.main {
  display: flex;
  justify-content: space-evenly;
  margin-top: 24px;
}
.main .left,
.main .right {
  border: 1px solid #dcdfe6;
  border-radius: 12px;
  width: 48%;
  height: 670px;
}
.left p,
.right p {
  color: #1d165c;
  font-weight: 700;
  margin-bottom: 10px;
}
h3 {
  text-align: center;
}
</style>


